.courseMainContainer {
  background-color: rgb(59, 143, 197);
}

.courseContainer {
  color: white;
  padding-left: 10%;
  padding-right: 10%;
  padding-top: 5px;
  padding-bottom: 10px;
}

.courseContainer > h2 {
  text-align: center;
  padding-bottom: 20px;
}

.courseContainerMain {
  display: flex;
}

.courseContainerMain > div {
  width: 50%;
  min-height: 500px;
}
.courseColumn {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.courseColumn h3,
.courseColumn p {
  text-align: left;
  width: 200px;
  display: grid;
  grid-template-columns: 125px 1fr;
  text-transform: capitalize;
}
.courseColumn p {
  font-size: 1rem;
}
.courseColumn span {
  letter-spacing: 2px;
}

.courseColumn img {
  border-radius: 50%;
  margin-bottom: 30px;
  width: 170px;
  height: 170px;
  /* box-shadow: 0 0 0 6px rgb(59, 143, 197), 0 0 0 12px #fff, 0 -12px 30px 0 #000,
    0 12px 30px 0 #000; */
}

.courseColumn article {
  width: initial;
}
